@import './calendar';

.navbar-brand {
    margin-right: 0;

    img {
        width: 80%;
    }

    span {
        display: block;
        margin-top: 10px;
        font-size: 14px;
        color: #f3376c;
    }
}

// 1px solid #ecd5da
ul.left-menu {
    padding-left: 15px;
    li {
        margin: 0;
        border-bottom: 1px solid #ecd5da;
        padding: 10px 0;
    }
}

.white-grad {
    background: linear-gradient(to bottom, rgba(255,255,255,1), rgba(255,255,255,1), rgba(255,255,255,0));
}

.tm-sec {
    background-image: url(../img/sai-tm-bg.svg), linear-gradient(180deg, #fff 0%, #fff 50%, #dc506c 50%, #dc506c 100%);
    background-position: -100px, center;
    background-repeat: no-repeat;
}

.services-sec {
    background: url(../img/services-bg.png) no-repeat top left;
    background-color: #fff8d7 !important;
    background-size: cover;
}

